import styles from './css/SponsorImage.module.css';
import sponsor1 from './image/Sponsor1.png';
import sponsor2 from './image/Sponsor2.png';
import vacuum from './image/null.png'
const SponsorsConst = [
    {id:3, image: vacuum, url: '/'},
    {id:1, image: sponsor1, url: 'https://vk.com/galaxy_empire_siths'},
    {id:2, image: sponsor2, url: 'https://vk.com/b.s.team'},
    {id:4, image: vacuum, url: '/'},
]

let SponsorsLet = SponsorsConst.map(function (props){
    return (
        <div className={styles.SponsorImage}>
            <a href={props.url}>
                <img src={props.image} alt={props.id} className={styles.Sponsor}/>
            </a>
        </div>
    )
});

const Sponsors = () => {
    return (
        <section className={styles.SponsorsSection}>
            {SponsorsLet}
        </section>
    );
};

export default Sponsors;