import styles from './css/Rules.module.css';
import RulesPhoto from './image/RulesPhoto.png';
import FilesItem from "./File";

const Rules = () => {
    return (
        <section className={styles.RulesSection}>
            <div className={styles.RulesTitle}>
                Тех.особенности Street Drift Competition
                <div className={styles.RulesFlex}>
                    <div className={styles.Files}>
                        <div className={styles.File}>
                            <FilesItem/>
                        </div>
                    </div>
                    <div className={styles.RulesPhoto}>
                        <img src={RulesPhoto} alt="Photo"/>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Rules;