import styles from './css/File.module.css'
import DOCX from './image/DOCX.png';
import Download from "./image/Download.png";

const FilesItem = () => {
    return (
        <div className={styles.ContainerWrap}>
            <div className={styles.ContainerItem}>
                <a href="https://docs.google.com/document/d/1ia9eqXjaAvAkv8GvFaN39br2BypUcphT9GDDJB4wUYs/edit?usp=sharing">
                    <div className={styles.PDFFile}>
                        <img src={DOCX} alt=""/>
                        <div className={styles.File} key={3} typeof="file">
                            Регламент
                        </div>
                    </div>
                </a>
            </div>
            <div className={styles.ContainerItem}>
                <a href="https://docs.google.com/document/d/1ia9eqXjaAvAkv8GvFaN39br2BypUcphT9GDDJB4wUYs/edit?usp=sharing">
                    <div className={styles.DownloadFile}>
                        <img src={Download} alt=""/>
                        <div className={styles.File} key={3} typeof="file">
                            Карпак WDT Street
                        </div>
                    </div>
                </a>
            </div>
        </div>
    );
};

export default FilesItem;